.terminalWindow {
	.terminal {
		width: 93vw;
		height: 93%;
		opacity: 0;
		pointer-events: none;
		transition: all ease .3s;
	
		&.active {
			width: 100vw;
			height: 100%;
			opacity: 1;
			pointer-events: all;
		}
		
		&.minimize {
			width: 1000px;
			height: 600px;
		}
	}
	
	.Terminal {
		background: #000;
		padding: 15px 5px;
		height: 100vh;
	
		* {
		  margin:0;
		  font-family:monospace;
		  color: #fff;
		  font-size: 14px;
		}
	
		pre {
		display: -webkit-box;

		&#input input {
			appearance: none;
			-webkit-appearance: none;
			border: none;
			outline: none;
			background-color: transparent;
			margin-left: 5px;
			width: 100%;
		}
	
		@keyframes blink {
		from, to {
			background-color: transparent;
		  }
		50% {
				background-color: #fff;
		  }
		}
		}
	}	
}